.page {
  padding-left: 25px;
}

.pageTitle {
  font-size: xxx-large;
  /* font-weight: 600; */
}

.sectionTitle {
  font-size: x-large;
}

.dataList {
  --width: 1px;
  --radius: 10px;
  --colour: black;
  --margin: 10px;

  list-style-type: none;
  padding: 10px;
  margin-left: var(--margin);
  margin-right: var(--margin);
  margin-bottom: var(--margin);
  border-left: var(--width) solid var(--colour);
  border-right: var(--width) solid var(--colour);
  border-bottom: var(--width) solid var(--colour);
  border-bottom-left-radius: var(--radius);
  border-bottom-right-radius: var(--radius);
}

.outerList {
  border-top: var(--width) solid var(--colour);
  border-top-left-radius: var(--radius);
  border-top-right-radius: var(--radius);
  max-width: 1000px;
}

.item {
  padding: 3px;
}

.components {
  display: flex;
  /* height: 40px; */
  border: var(--width) solid var(--colour);
}

.components:hover {
  background-color: beige;
}

.leftColumn {
  margin: 5px;
  flex-grow: 1;
}

.rightColumn {
  margin: 5px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.fields {
  display: grid;
  grid-template-columns: 80px 125px;
  gap: 5px;
  margin: 5px;
}

.data {
  display: flex;
  flex-grow: 1;
}

.climbData {
  display: flex;
  flex-direction: column;
}
.icons {
  display: flex;
}

.colourBar {
  color: #bdbdbd;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 30px;
}

.hex {
  width: 50px;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

.hexImage {
  width: 35px;
  height: 35px;
}

.text {
  padding: 3px;
}

.date {
  align-self: center;
  padding-right: 10px;
}

.buttons {
  display: flex;
  margin: 5px;
}

.buttons > * {
  margin: 3px;
}

img {
  width: 25px;
}

.topButtons {
  margin: 5px;
  display: flex;
  justify-content: space-between;
}

